import React,{Fragment,PureComponent} from "react";
import '../../../style/admin.css';
import axios from 'axios';
import {connect} from 'react-redux';
import {actionCreators} from "../store";
import $ from 'jquery';


class Configure extends PureComponent{
    constructor(props){
        super(props);
        this.state = {
            isToggleOn: false,
            mapAddress:'',
            mapName:'normal',
            isStand:false,
            isLogin:false,
            alarmTest:false,//
            device: {},//
            deviceId:'',//
        };
        Configure.this = this;
        this.onConfig = this.onConfig.bind(this); //
        this.addressChange = this.addressChange.bind(this); //
        this.handleChange = this.handleChange.bind(this); //
        this.handleIsStand = this.handleIsStand.bind(this);//
        this.handleIsLogin = this.handleIsLogin.bind(this); //
        this.configSave = this.configSave.bind(this);//
        this.AlarmTest = this.AlarmTest.bind(this);//
    }
    componentDidMount() {
        this.onConfig();
    }

    //
    onConfig(){
        var that =this;
        axios.get(global.Url+'sys/config').then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                sessionStorage.setItem('adminTitle',result.sys_name);
                that.state=({
                    homeTitle:result.sys_name
                });
                sessionStorage.setItem('Serverurl',result.data_server_uri);
                sessionStorage.setItem('data_type',result.data_type);
                this.setState({
                    mapAddress:result.data_server_uri,
                    mapName:result.data_type,
                    isStand:result.is_stand,
                    isLogin:result.is_login,
                })
            }else{
                alert(res.data.msg)
            }
        })

        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn,
            Display: prevState.isToggleOn ? 'none': 'block'
        }));
    }
    //
    addressChange = (event) => {
        this.setState({
            mapAddress: event.target.value
        });
    }
    //
    handleChange=(event)=>{
        //
        this.setState({
            mapName:event.target.value
        })
    }
    //
    handleIsStand=(event)=>{
        //
        this.setState(prevState => ({
            isStand: !prevState.isStand
        }));
    }
    //
    handleIsLogin = (event) => {
        this.setState(prevState => ({
            isLogin: !prevState.isLogin
        }));
    }
    //
    configSave = (mapAddress,mapName,isStand,isLogin) => {
        console.log(mapAddress,mapName,isStand,isLogin,global.Database);
        axios.post(global.Url+'sys/config/updatePro',{
            data_server_uri:mapAddress,
            data_type:mapName,
            is_stand:isStand,
            is_login:isLogin,
            })
             .then(function (res) {
                const result = res.data.data;
                console.log(result);
                 if(result) {
                     sessionStorage.setItem('Serverurl',result.data_server_uri);
                     sessionStorage.setItem('data_type',result.data_type);
                     var frame=document.getElementById("core_content");
                     var sendData={
                         Event : 'RefreshPage()',
                         ModName : "",
                         parameter : {}
                     };
                     frame.contentWindow.postMessage(sendData,"*");

                 }else{
                     alert(res.data.msg)
                 }

            })
             .catch(function (error) {
                console.log(error);
            })
        this.props.navClick(100);

    }
    //
    AlarmTest(){
        $(".Basic").css({'display':'block'})
        this.setState({
            alarmTest:true
        })
        axios.post(global.Url+'device/category/list',{enable:true}).then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                this.setState({
                    device:result,
                    deviceId:result[0].category_name
                })
            }else{
                alert(res.data.msg)
            }
        })
    }
    render(){
        const {mapAddress,mapName,isStand,isLogin,alarmTest,device,deviceId} = this.state;
        const{adminItem,navClick} = this.props;
        return(
            <Fragment>
                <li className={adminItem === 1 ? 'cur' : ''} onClick={() => navClick(1)}></li>
                {adminItem === 1
                    ?
                    <div>
                        <div className="configShow">
                                <div className="configShowNav"> <i onClick={() => navClick(100)}></i></div>
                                <div className="configShowConent">
                                    <div className="configShowConent_cont">
                                        <li><span></span><input type="text" value={mapAddress} onChange={(event) => this.addressChange(event)} /></li>
                                        <li><span></span>
                                            <input type="radio" name='' value="normal" checked={mapName === 'normal'} onChange={(e) => this.handleChange(e)}  /><label></label>
                                            <input type="radio" name='' value="virtual" checked={mapName === 'virtual'} onChange={(e) => this.handleChange(e)} /><label></label>
                                            <div className="dot">gender: {}</div>
                                        </li>
                                        <li><span></span><input type="checkbox" checked={isStand} onChange={(e) => this.handleIsStand(e)} /></li>

                                        <li><span></span><input type="checkbox" checked={isLogin} onChange={(e) => this.handleIsLogin(e)} /></li>
                                    </div>

                                    <div className="configBut"><button onClick={() => this.configSave(mapAddress,mapName,isStand,isLogin)}></button><button></button><button onClick={()=>this.AlarmTest()}></button></div>
                                </div>
                            </div>
                        {alarmTest?
                            <div className="configShow Basic" style = { { marginLeft: '520px'} }>
                                <div className="configShowNav"> <i onClick={() => navClick(101)}></i></div>
                                <div className="configShowConent">
                                    <div className="configShowConent_cont DevicePic">
                                        <li><span></span>
                                            <select
                                                onChange={this.selectChange}
                                                className='AddSelect'
                                                value = {deviceId}>
                                                {
                                                    device.map((item,i) => {
                                                        return (
                                                            <option key={"op"+i} index={i} value={item.category_name}>{item.category_name}</option>
                                                        )
                                                    })
                                                }
                                            </select>
                                        </li>
{/*                                        <li><span></span>
                                            <select
                                                onChange={this.selectChange}
                                                className='AddSelect'
                                                value = {deviceId}>
                                                {
                                                    device.map((item,i) => {
                                                        return (
                                                            <option key={"op"+i} index={i} value={item.category_name}>{item.category_name}</option>
                                                        )
                                                    })
                                                }
                                            </select>
                                        </li>*/}
                                    </div>
                                </div>
                            </div>
                            :''
                        }
                    </div>
                    :""
                }

            </Fragment>

        )
    }
}
const mapState = (state) => {
    return{
        adminItem:state.admin.get('adminItem')
    }
};
const mapDispatch = (dispatch) => ({
    navClick(item) {
        if(item === 100){
            dispatch(actionCreators.nav_click(item));
        }else{
            dispatch(actionCreators.nav_click(item));
            axios.get(global.Url+'sys/config').then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    sessionStorage.setItem('adminTitle',result.sys_name);
                    sessionStorage.setItem('Serverurl',result.data_server_uri);
                    sessionStorage.setItem('data_type',result.data_type);
                    Configure.this.setState({
                        mapAddress:result.data_server_uri,
                        mapName:result.data_type,
                        isStand:result.is_stand,
                        isLogin:result.is_login
                    })
                }else{
                    alert(res.data.msg)
                }
            })
        }

    },
});

export default connect(mapState,mapDispatch)(Configure);